import React, { useState, useMemo } from 'react';
import { Container, Form, Button, Spinner } from 'react-bootstrap';
import axios from 'axios';
import { useReactTable, getCoreRowModel, flexRender } from '@tanstack/react-table';

const LoadingSpinner = () => <Spinner animation="border" />;

const FormField = ({ label, controlId, value, onChange, options }) => (
  <Form.Group controlId={controlId}>
    <Form.Label>{label}</Form.Label>
    <Form.Control as="select" value={value} onChange={onChange}>
      <option value="">Select {label}</option>
      {options.map((option) => (
        <option key={option.value} value={option.value}>
          {option.label}
        </option>
      ))}
    </Form.Control>
  </Form.Group>
);

const ChangeCollabSpace = () => {
  const [formData, setFormData] = useState({
    sourceCollabSpace: '',
    targetCollabSpace: '',
    sourceOrganization: '',
    targetOrganization: '',
  });
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSourceChange = async (event) => {
    const selectedValue = event.target.value;
    setFormData((prev) => ({ ...prev, sourceCollabSpace: selectedValue }));
    setIsLoading(true);

    try {
      // Example API request
      // const response = await axios.get(`https://api.example.com/data?source=${selectedValue}`);
      // setData(response.data);
    } catch (error) {
      console.error('Error fetching data:', error);
      setError('Error fetching data. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // Handle form submission logic here
  };

  const columns = useMemo(
    () => [
      { accessorKey: 'title', header: 'Title' },
      { accessorKey: 'name', header: 'Name' },
      { accessorKey: 'description', header: 'Description' },
      { accessorKey: 'owner', header: 'Owner' },
      { accessorKey: 'currentCollabSpace', header: 'Current Collab-Space' },
    ],
    []
  );

  const table = useReactTable({
    data,
    columns,
    getCoreRowModel: getCoreRowModel(),
    state: {
      pagination: {
        pageIndex: 0,
        pageSize: 5,
      },
    },
  });

  console.log('Table State:', table.getState());

  return (
    <Container className="mt-4">
      <h1>Change Ownership</h1>
      <Form onSubmit={handleSubmit}>
        <FormField
          label="Source Collab-Space"
          controlId="sourceCollabSpace"
          value={formData.sourceCollabSpace}
          onChange={handleSourceChange}
          options={[
            { value: 'source1', label: 'Source 1' },
            { value: 'source2', label: 'Source 2' },
            { value: 'source3', label: 'Source 3' },
          ]}
        />
        <FormField
          label="Target Collab-Space"
          controlId="targetCollabSpace"
          value={formData.targetCollabSpace}
          onChange={handleChange}
          options={[
            { value: 'target1', label: 'Target 1' },
            { value: 'target2', label: 'Target 2' },
            { value: 'target3', label: 'Target 3' },
          ]}
        />
        <FormField
          label="Source Organization"
          controlId="sourceOrganization"
          value={formData.sourceOrganization}
          onChange={handleChange}
          options={[
            { value: 'org1', label: 'Organization 1' },
            { value: 'org2', label: 'Organization 2' },
            { value: 'org3', label: 'Organization 3' },
          ]}
        />
        <FormField
          label="Target Organization"
          controlId="targetOrganization"
          value={formData.targetOrganization}
          onChange={handleChange}
          options={[
            { value: 'org1', label: 'Organization 1' },
            { value: 'org2', label: 'Organization 2' },
            { value: 'org3', label: 'Organization 3' },
          ]}
        />
        <Button variant="primary" type="submit" className="mt-3 float-end">
          Submit
        </Button>
      </Form>
      {isLoading ? (
        <LoadingSpinner />
      ) : (
        <>
          {error && <div className="alert alert-danger">{error}</div>}
          <table className="table mt-4">
            <thead>
              {table.getHeaderGroups().map((headerGroup) => (
                <tr key={headerGroup.id}>
                  {headerGroup.headers.map((column) => (
                    <th key={column.id}>{flexRender(column.columnDef.header, column.getContext())}</th>
                  ))}
                </tr>
              ))}
            </thead>
            <tbody>
              {table.getRowModel().rows.map((row) => (
                <tr key={row.id}>
                  {row.getVisibleCells().map((cell) => (
                    <td key={cell.id}>{flexRender(cell.column.columnDef.cell, cell.getContext())}</td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>

          <div className="pagination">
            <Button onClick={() => table.setPageIndex(0)} disabled={!table.getCanPreviousPage()}>
              {'<<'}
            </Button>
            <Button onClick={() => table.previousPage()} disabled={!table.getCanPreviousPage()}>
              {'<'}
            </Button>
            <Button onClick={() => table.nextPage()} disabled={!table.getCanNextPage()}>
              {'>'}
            </Button>
            <Button onClick={() => table.setPageIndex(table.getPageCount() - 1)} disabled={!table.getCanNextPage()}>
              {'>>'}
            </Button>
            <span>
              Page {table.getState().pagination.pageIndex + 1} of {table.getPageCount()}
            </span>
            <select
              value={table.getState().pagination.pageSize}
              onChange={(e) => table.setPageSize(Number(e.target.value))}
            >
              {[5, 10, 20].map((size) => (
                <option key={size} value={size}>
                  Show {size}
                </option>
              ))}
            </select>
          </div>
        </>
      )}
    </Container>
  );
};

export default ChangeCollabSpace;